import View							= require("awayjs-core/lib/containers/View");
import DirectionalLight				= require("awayjs-core/lib/entities/DirectionalLight");
import Mesh							= require("awayjs-core/lib/entities/Mesh");
import Skybox						= require("awayjs-core/lib/entities/Skybox");
import AwayEvent					= require("awayjs-core/lib/events/Event");
import LoaderEvent					= require("awayjs-core/lib/events/LoaderEvent");
import Vector3D						= require("awayjs-core/lib/core/geom/Vector3D");
import AssetLibrary					= require("awayjs-core/lib/core/library/AssetLibrary");
import URLLoader					= require("awayjs-core/lib/core/net/URLLoader");
import URLLoaderDataFormat			= require("awayjs-core/lib/core/net/URLLoaderDataFormat");
import URLRequest					= require("awayjs-core/lib/core/net/URLRequest");
import TriangleMethodMaterial		= require("awayjs-stagegl/lib/materials/TriangleMethodMaterial");
import ParserUtils					= require("awayjs-core/lib/parsers/ParserUtils");
import PrimitiveTorusPrefab			= require("awayjs-core/lib/prefabs/PrimitiveTorusPrefab");
import PerspectiveProjection		= require("awayjs-core/lib/projections/PerspectiveProjection");
import StaticLightPicker			= require("awayjs-core/lib/materials/lightpickers/StaticLightPicker");
import DefaultRenderer				= require("awayjs-stagegl/lib/core/render/DefaultRenderer");
import ImageTexture					= require("awayjs-core/lib/textures/ImageTexture");
import RequestAnimationFrame		= require("awayjs-core/lib/utils/RequestAnimationFrame");

class TorusPrimitive
{
	private _view:View;
	private _torus:PrimitiveTorusPrefab;
	private _mesh:Mesh;
	private _raf:RequestAnimationFrame;
	private _image:HTMLImageElement;
	private _texture:ImageTexture;
	private _material:TriangleMethodMaterial;
	private _light:DirectionalLight;
	private _lightPicker:StaticLightPicker;

	constructor ()
	{
		this.initView();

		this._raf = new RequestAnimationFrame(this.render, this);
		this._raf.start(); // Start the frame loop ( request animation frame )

		this.loadResources(); // Start loading the resources
		window.onresize = (event:UIEvent) => this.resize(event); // Add event handler for window resize

		this.resize();
	}

	/**
	 *
	 */
	private initView()
	{
		this._view = new View(new DefaultRenderer());// Create the Away3D View
		this._view.backgroundColor = 0x000000;// Change the background color to black
	}

	/**
	 *
	 */
	private loadResources()
	{
		var urlRequest:URLRequest = new URLRequest ("assets/dots.png");
		var imgLoader:URLLoader = new URLLoader(); // Image Loader
		imgLoader.dataFormat = URLLoaderDataFormat.BLOB;

		imgLoader.addEventListener (AwayEvent.COMPLETE, (event:AwayEvent) => this.urlCompleteHandler(event)); // Add event listener for image complete
		imgLoader.load(urlRequest); // start loading
	}

	/**
	 *
	 * @param event
	 */
	private urlCompleteHandler (event:AwayEvent)
	{
		var imageLoader:URLLoader = <URLLoader> event.target
		this._image = ParserUtils.blobToImage(imageLoader.data);
		this._image.onload = (event:Event) => this.imageCompleteHandler(event);
	}

	/**
	 *
	 */
	private initLights()
	{
		this._light = new DirectionalLight(); // Create a directional light
		this._light.diffuse = .7;
		this._light.specular = 1;
		this._view.scene.addChild(this._light);
		this._lightPicker = new StaticLightPicker([this._light]); // Create a light picker
	}

	/**
	 *
	 */
	private initMaterial(image:HTMLImageElement)
	{
		this._texture = new ImageTexture(image, false); // Create a texture
		this._material = new TriangleMethodMaterial (this._texture, true, true, false); // Create a material
		this._material.lightPicker = this._lightPicker; // assign the lights to the material
	}

	/**
	 *
	 */
	private initTorus()
	{
		this._torus = new PrimitiveTorusPrefab(220, 80, 32, 16, false); // Create the Torus prefab
		this._mesh = <Mesh> this._torus.getNewObject(); //Create the mesh
		this._mesh.material = this._material; //apply the material
		this._view.scene.addChild(this._mesh); // Add the mesh to the scene
	}

	/**
	 *
	 */
	private imageCompleteHandler(event:Event)
	{
		this.initLights ();
		this.initMaterial(<HTMLImageElement> event.target);
		this.initTorus ();
	}

	/**
	 *
	 */
	public render(dt:number = null)
	{
		if (this._mesh)
			this._mesh.rotationY += 1;

		this._view.render ();
	}

	/**
	 *
	 */
	public resize(event:UIEvent = null)
	{
		this._view.y = 0;
		this._view.x = 0;
		this._view.width = window.innerWidth;
		this._view.height = window.innerHeight;
	}
}

window.onload = function()
{
	new TorusPrimitive(); // Start the demo
}